<template>
  <div>
    <v-toolbar color="teal darken-3">
      <v-toolbar-title class="white--text font-weight-bold" text-md-center>
        <span>{{ userName }}</span>
        <v-spacer></v-spacer>
      </v-toolbar-title>
    </v-toolbar>
    <v-content>
      <v-container>
        <div class="text-xs-center">
          <v-chip color="teal lighten-4" class="mb-3" text-color="dark">{{monthTitle}}</v-chip>
        </div>

        <v-card color="teal lighten-5" class="dark--text">
          <v-card-title>
            <div>
              <div class="title font-weight-light my-2">
                Total Work :
                <span class="font-weight-regular">₹ {{totalWorkRs}}</span>
              </div>
              <v-divider color="black"></v-divider>
              <div class="title font-weight-light my-2">
                Received Amount :
                <span class="font-weight-regular">₹ {{receiveRs}}</span>
              </div>
              <v-divider color="black"></v-divider>
              <div class="title font-weight-light my-2">
                Amount Crrent Status :
                <span class="font-weight-regular">₹ {{currentRs}}</span>
              </div>
            </div>
          </v-card-title>
          <v-card-actions>
            <v-dialog v-model="search" max-width="250px">
              <v-card>
                <v-card-title class="title">Month Data</v-card-title>
                <v-card-text class="py-1">
                  <v-text-field label="Select Month and Date : " v-model="searchdate"></v-text-field>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn flat color="teal darken-3" @click="search = false">Cancel</v-btn>
                  <v-btn flat color="teal darken-3" @click="searchData(false)">Done</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-btn icon flat color="teal darken-3" @click="previous()">
              <v-icon>navigate_before</v-icon>
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn flat color="teal darken-3" class="font-weight-bold" @click="search = true">
              <v-icon left>search</v-icon>Search
            </v-btn>
            <v-btn color="teal darken-3" class="font-weight-bold" flat @click="pay = true">
              <v-icon left>payment</v-icon>Payment
            </v-btn>

            <v-spacer></v-spacer>
            <v-btn icon color="teal darken-3" class="font-weight-bold" flat @click="next()">
              <v-icon>navigate_next</v-icon>
            </v-btn>
          </v-card-actions>
        </v-card>
        <v-dialog v-model="pay" max-width="250px">
          <v-card>
            <v-card-title class="title">Give Money.</v-card-title>
            <v-card-text class="py-1">
              <v-text-field label="Eneter Payment Amount : " v-model="money"></v-text-field>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn flat color="teal darken-3" @click="pay = false">Cancel</v-btn>
              <v-btn flat color="teal darken-3" @click="payment(false)">Done</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <v-dialog v-model="loading" max-width="220px">
          <v-card>
            <v-card-title>
              <v-flex class="ma-3">
                <div class="title text-xs-center">
                  <span class="my-2">Loading...</span>
                </div>
                <div class="text-xs-center">
                  <v-progress-circular indeterminate color="teal darken-3" class="mt-3"></v-progress-circular>
                </div>
              </v-flex>
            </v-card-title>
          </v-card>
        </v-dialog>

        <v-data-table
          :headers="headers"
          :items="data"
          class="elevation-1"
          hide-actions
          disable-initial-sort
          color="teal lighten-2"
        >
          <template v-slot:items="props">
            <td class="text-xs-left">{{ props.item.displayDate || props.item.date }}</td>
            <td class="text-xs-left">{{ props.item.zarirs }}</td>
            <td class="text-xs-left">{{ props.item.thours }}</td>
            <td class="text-xs-left">{{ props.item.rs }}</td>
            <td class="text-xs-left">{{ props.item.money }}</td>
            <td class="justify-center layout px-0">
              <v-icon color="teal darken-1" @click="del(props)">delete</v-icon>
            </td>
          </template>
        </v-data-table>

        <v-btn absolute dark fab top right color="teal darken-1" @click="dialog = !dialog">
          <v-icon>add</v-icon>
        </v-btn>

        <v-dialog v-model="dialog" max-width="500px">
          <v-card>
            <v-card-text>
              <v-form ref="form">
                <v-flex>
                  <v-menu
                    ref="menu"
                    v-model="menu"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    :return-value.sync="date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on }">
                      <v-text-field
                        v-model="date"
                        label="Picker in menu"
                        prepend-icon="event"
                        readonly
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker v-model="date" no-title scrollable>
                      <v-spacer></v-spacer>
                      <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
                      <v-btn flat color="primary" @click="$refs.menu.save(date)">OK</v-btn>
                    </v-date-picker>
                  </v-menu>
                </v-flex>

                <v-flex>
                  <v-menu
                    ref="menu2"
                    v-model="menu2"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    :return-value.sync="stime"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    max-width="290px"
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on }">
                      <v-text-field
                        v-model="stime"
                        label="Sart Time"
                        prepend-icon="access_time"
                        readonly
                        required
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-time-picker
                      v-if="menu2"
                      v-model="stime"
                      full-width
                      format="24hr"
                      @click:minute="$refs.menu2.save(stime)"
                    >{{stime}}</v-time-picker>
                  </v-menu>
                </v-flex>

                <v-flex>
                  <v-menu
                    ref="menu3"
                    v-model="menu3"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    :return-value.sync="etime"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    max-width="290px"
                    min-width="290px"
                    required
                  >
                    <template v-slot:activator="{ on }">
                      <v-text-field
                        v-model="etime"
                        label="End Time"
                        prepend-icon="access_time"
                        readonly
                        v-on="on"
                        allowed-minutes
                      ></v-text-field>
                    </template>
                    <v-time-picker
                      v-if="menu3"
                      v-model="etime"
                      full-width
                      format="24hr"
                      @click:minute="$refs.menu3.save(etime)"
                    ></v-time-picker>
                  </v-menu>
                </v-flex>

                <v-flex>
                  <v-text-field
                    label="Loss Time"
                    placeholder="Loss Time"
                    prepend-icon="alarm"
                    v-model="lostime"
                  ></v-text-field>
                </v-flex>

                <v-flex>
                  <v-text-field
                    label="Zari Rs"
                    placeholder="Zari Rs"
                    prepend-icon="attach_money"
                    v-model="zarirs"
                    required
                  ></v-text-field>
                </v-flex>

                <v-flex>
                  <v-text-field
                    label="Give Money"
                    placeholder="Give Money (optinol)"
                    prepend-icon="attach_money"
                    v-model="money"
                  ></v-text-field>
                </v-flex>
              </v-form>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn flat color="teal darken-3" @click="dialog = false">Cancel</v-btn>
              <v-btn flat color="teal darken-3" @click="submit(false)">Submit</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-container>
    </v-content>
  </div>
</template>

<script>
import moment, { months } from "moment";
import firebase from "firebase";

export default {
  data() {
    return {
      pay: false,
      search: false,
      dialog: false,
      labourDataId: "",
      labaurname: "",
      date: new Date().toISOString().substr(0, 10),
      menu: false,
      modal: false,
      menu2: false,
      menu3: false,
      modal2: false,
      stime: null,
      etime: null,
      lostime: null,
      zarirs: null,
      thours: null,
      trs: null,
      totalWorkRs: 0,
      totalHours: 0,
      receiveRs: 0,
      currentRs: 0,
      money: 0,
      headers: [
        { text: "Date", value: "date" },
        { text: "Zari Rate", value: "zarirs" },
        { text: "Hour / Day", value: "thours" },
        { text: "Rs / Hour", value: "rs" },
        { text: "ReceivedRs", value: "money" },
        { text: "Actions", value: "name" }
      ],
      data: [],
      monthData: 0,
      userName: "Loading....",
      loading: false,
      Laboursid: "",
      monthTitle: "",
      currentmonth: "",
      searchdate: ""
    };
  },
  async mounted() {
    this.loading = true;
    this.Laboursid = this.$route.params.id;
    await this.checkFirstday();
    const { month, monthName, year } = this.getMontNamefromDatabse(this.date);
    this.monthTitle = monthName + " " + year;
    this.currentmonth = year + "-" + month;

    // home se user id aarhi h
    var db = firebase.firestore();
    var userRef = await db.collection("Labours").doc(this.Laboursid);
    userRef.get().then(doc => {
      this.userName = doc.data().name;
    });
    this.getDataFromDatabase(year + "-" + month);
  },
  methods: {
    async checkFirstday() {
      const date = new Date().toISOString().substr(0, 10);
      // const date = "2019/08/09";
      const { month, monthName, year } = this.getMontNamefromDatabse(date);
      this.monthTitle = monthName + " " + year;
      this.currentmonth = year + "-" + month;
      await this.getDataFromDatabase(this.currentmonth);
      if (this.data.length === 0) {
        const previousMontData = this.getPriviousMonth(this.currentmonth, true);
        const { month, year } = this.getMontNamefromDatabse(previousMontData);
        const prevMonthID = year + "-" + month;
        var db = firebase.firestore();
        await db
          .collection("LaboursData") //
          .where("labourId", "==", this.Laboursid)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              const data = doc.data()[prevMonthID];
              for (var sdata of data) {
                if (sdata.rs != "-")
                  this.totalWorkRs = +this.totalWorkRs + +sdata.rs;
                if (sdata.money != "-")
                  this.receiveRs = +this.receiveRs + +sdata.money;
                this.currentRs = this.totalWorkRs - this.receiveRs;
              }
              // add to firebase to this month pehli entry
              if (this.currentRs < 0) {
                this.addDataToFirebase({
                  date: date,
                  lostime: "-",
                  zarirs: "-",
                  money: this.currentRs * -2 + this.currentRs,
                  thours: "-",
                  rs: "Advance Rs."
                });
              }
              if (this.currentRs > 0) {
                this.addDataToFirebase({
                  date: date,
                  lostime: "-",
                  zarirs: "-",
                  money: "-",
                  thours: "To be Paid Amount.",
                  rs: this.currentRs
                });
              }
            });
          });
      }
      return;
    },
    getMontNamefromDatabse(displaymonthData) {
      var check = moment(displaymonthData, "YYYY/MM/DD");
      var year = check.format("YYYY");
      var month = check.format("MM");
      // this.monthData = year + "-" + month;
      return {
        month: month,
        monthName: moment()
          .month(month - 1)
          .format("MMMM"),
        year: year
      };
    },
    async getDataFromDatabase(reciveMonthData) {
      this.loading = true;
      var db = firebase.firestore();
      await db
        .collection("LaboursData") //
        .where("labourId", "==", this.Laboursid) //database hsr ek ki labarid ko utha k Labourid se cheek krega
        .get() // jo id match krega uska data get krea
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            //doc.data() is never undefined for query doc snapshots
            this.labourDataId = doc.id;
            if (doc.data()[reciveMonthData]) {
              this.data = doc.data()[reciveMonthData];
            } else {
              this.data = [];
            }
          });
          this.calculateTotals();
          this.loading = false;
          return;
        });
    },

    calculateTotals() {
      this.receiveRs = 0;
      this.totalWorkRs = 0;
      this.totalHours = 0;
      this.currentRs = 0;
      if (this.data.length > 0) {
        this.data = this.data.filter(item => {
          return item.zarirs != "Total";
        });
        for (var sdata of this.data) {
          if (sdata.rs != "-" && sdata.rs != "Advance Rs.") {
            this.totalWorkRs = +this.totalWorkRs + +sdata.rs;
          }
          if (sdata.money != "-") {
            this.receiveRs = +this.receiveRs + +sdata.money;
          }
          if (sdata.thours != "-" && sdata.thours != "To be Paid Amount.") {
            this.totalHours = +this.totalHours + +sdata.thours;
          }
          this.currentRs = this.totalWorkRs - this.receiveRs;
        }
        this.data.push({
          date: "",
          lostime: "",
          zarirs: "Total",
          money: this.receiveRs,
          thours: this.totalHours,
          rs: this.totalWorkRs
        });
      }
    },
    del(pos) {
      this.loading = true;
      var db = firebase.firestore();
      var docref = db.collection("LaboursData").doc(this.labourDataId);
      const { year, month } = this.getMontNamefromDatabse(this.currentmonth);
      docref
        .update({
          [year + "-" + month]: firebase.firestore.FieldValue.arrayRemove(
            pos.item
          )
        })
        .then(() => {
          const idx = this.data.indexOf(pos.item);
          this.data.splice(idx, 1);
          this.calculateTotals();
          this.loading = false;
        });
    },
    validate() {
      if (this.zarirs != null && this.stime != null && this.etime != null) {
        return true;
      } else {
        alert("Enter Filed data");
        return false;
      }
    },
    submit(con) {
      if (this.validate()) {
        this.stime = "2019-7-4 " + this.stime;
        this.stime = moment(this.stime);
        this.etime = "2019-7-4 " + this.etime;
        this.etime = moment(this.etime);
        if (this.etime.isBefore(this.stime)) this.etime.add(1, "day");
        this.thours = moment
          .duration(
            moment(this.etime, "YYYY/MM/DD HH:mm a").diff(
              moment(this.stime, "YYYY/MM/DD HH:mm a")
            )
          )
          .asMinutes();

        this.addDataToFirebase({
          date: this.date,
          lostime: this.lostime,
          zarirs: this.zarirs,
          money: this.money,
          thours: this.thours / 60 - this.lostime,
          rs: (this.thours / 60 - this.lostime) * this.zarirs
        });
        this.money = 0;
        this.dialog = con;
        this.etime = null;
        this.stime = null;
        this.lostime = 0;
        this.zarirs = 0;
        this.thours = 0;
        this.trs = 0;
      }
    },
    addDataToFirebase(data) {
      const { month, monthName, year } = this.getMontNamefromDatabse(data.date);
      var db = firebase.firestore();
      var doc = db.collection("LaboursData").doc(this.labourDataId);
      data.displayDate = monthName + " " + data.date.split("-")[2];
      if (data.rs != "-" && data.rs != "Advance Rs.") {
        data.rs = Math.floor(data.rs * 100) / 100;
      }
      if (data.thours != "-" && data.thours != "To be Paid Amount.") {
        data.thours = Math.floor(data.thours * 100) / 100;
      }
      if (data.money != "-") {
        data.money = Math.floor(data.money * 100) / 100;
      }
      doc
        .update({
          [year + "-" + month]: firebase.firestore.FieldValue.arrayUnion(data)
        })
        .then(() => {
          if (year + "-" + month === this.currentmonth) {
            this.data.push(data); //offline push
          }
          this.calculateTotals();
        });
    },
    searchData(con) {
      this.search = con;
      this.loading = true;
      const { monthName, month, year } = this.getMontNamefromDatabse(
        this.searchdate
      );
      this.monthTitle = monthName + year;
      this.currentmonth = year + "-" + month;
      this.getDataFromDatabase(year + "-" + month);
      this.loading = false;
    },
    getPriviousMonth(date, previous) {
      const { year, month } = this.getMontNamefromDatabse(date);
      let monthReturn;
      if (previous) {
        if (month == "01") {
          monthReturn = +year - 1 + "/" + "12" + "/06";
        } else monthReturn = year + "/" + (+month - 1) + "/06";
        return monthReturn;
      }
      if (!previous) {
        if (month == "12") {
          monthReturn = +year + 1 + "/" + "01" + "/06";
        } else monthReturn = year + "/" + (+month + 1) + "/06";
        return monthReturn;
      }
    },
    previous() {
      const previousMonth = this.getPriviousMonth(this.currentmonth, true);
      const { monthName, month, year } = this.getMontNamefromDatabse(
        previousMonth
      );
      this.monthTitle = monthName + " " + year;
      this.currentmonth = year + "-" + month;
      this.getDataFromDatabase(year + "-" + month);
    },
    next() {
      const nextMonth = this.getPriviousMonth(this.currentmonth, false);
      const { monthName, month, year } = this.getMontNamefromDatabse(nextMonth);
      this.monthTitle = monthName + " " + year;
      this.currentmonth = year + "-" + month;
      this.getDataFromDatabase(year + "-" + month);
    },
    payment(con) {
      this.addDataToFirebase({
        date: this.date,
        lostime: "-",
        zarirs: "-",
        money: this.money,
        thours: "-",
        rs: "-"
      });
      this.money = 0;
      this.pay = con;
    }
  }
};
</script>